/*
// .th-logo--desktop
*/

.th-logo--desktop {
    display: flex;
    flex-direction: column;

    .th-logo__slogan {
        padding-top: 11px;
        font-size: 11px;
        line-height: 1;
        text-transform: uppercase;
    }
    .th-logo__image {
        display: none;
        align-items: center;
        height: 50px;
        flex-grow: 1;
    }
    .th-logo__title {
        line-height: 1;
        font-size: 32px;
        font-weight: bold;
        display: flex;
        align-items: center;
        justify-content: center;
        height: 50px;
        text-align: center;
        margin: 0;
        flex-grow: 1;
        text-transform: uppercase;
    }

    a,
    a:hover {
        color: inherit;
    }

    .wp-custom-logo & {
        .th-logo__image {
            display: flex;
        }
        .th-logo__title {
            display: none;
        }
    }

    @if($header-layout == spaceship) {
        .th-logo {
            display: flex;
            flex-direction: column;
        }

        .th-logo__image {
            justify-content: center;
            flex-grow: 1;
        }
        .th-header--without-topbar & .th-logo__slogan {
            display: none;
        }
    }
    @if($header-layout == classic) {
        .th-logo__image {
            height: 100%;
        }
        .th-logo__slogan {
            display: none;
        }
    }


    // define scheme
    @if($header-layout == spaceship) {
        .th-logo__slogan {
            color: map_get($header-scheme, muted);

            @include scheme-font-weight($header-scheme, muted, $font-weight-medium);
        }
    }

    svg {
        fill: currentColor;
    }

    .th-part-primary {
        color: map_get($logo-scheme, primary);
    }
    .th-part-secondary {
        color: map_get($logo-scheme, secondary);
    }

    .th-logo__image {
        img,
        svg {
            max-height: 66px;
            max-width: 300px;
            height: auto;
            width: auto;
        }
    }
}
