/*
// .th-dropcart
*/

$local-padding: 24px;
$local-image-size: 70px;
$local-image-margin: 10px;

.th-dropcart {
    width: 320px;
    background-color: map_get($menu-scheme, main);
    box-shadow: map_get($menu-scheme, shadow);
    border-radius: 1.5px;
    padding: $local-padding;
    color: map_get($menu-scheme, opposite);
}
.woocommerce-mini-cart {
    list-style: none;
    padding: 0;
    margin: -8px 0 0;
}
.woocommerce-mini-cart-item {
    @include th-reset-list-item;

    $local-content-padding-y: 6px;

    border-bottom: 1px solid map_get($menu-scheme, divider);
    padding-top: $local-content-padding-y;
    padding-bottom: ($local-content-padding-y + 8px);
    margin-bottom: 8px;
    position: relative;
    font-size: 15px;
    line-height: 18px;

    @include direction {
        #{$padding-inline-start}: 80px;
        #{$padding-inline-end}: (26px + 10px);
    }

    & > .blockUI {
        opacity: 0 !important;
        cursor: default !important;
    }

    img {
        width: 70px;
        height: auto;
        margin: -$local-content-padding-y 0;

        @include direction {
            #{$margin-inline-start}: -80px;
            float: $inline-start;
        }
    }

    .remove {
        $local-size: 26px;

        position: absolute;
        width: $local-size;
        height: $local-size;
        border-radius: $local-size / 2;
        display: flex;
        align-items: center;
        justify-content: center;
        padding: 0;
        border: none;
        font-size: 0;
        color: transparent;
        top: 0;
        transition:
            background .12s ease-in-out;

        @include direction {
            #{$inset-inline-end}: -($local-size / 2);
        }

        &:focus {
            outline: none;
        }

        &:before {
            @include svg-pseudo-element(10px);
            @include cross-10(#000);

            position: static;
            opacity: .2;
            transition:
                opacity .12s ease-in-out;

            @include direction {
                #{$inset-inline-start}: 5px;
            }
        }

        &:after {
            @include th-loader(14px, 2px, #000);

            display: none;
        }

        &:hover {
            background: #f2f2f2;

            &:before {
                opacity: .4;
            }
        }
        &:active {
            background: #ebebeb;
            transition-duration: 0s;
        }

        &.stroyka-loading {
            &:before {
                opacity: 0;
            }
            &:after {
                display: block;
            }
        }
    }

    &:after {
        display: block;
        content: '';
        clear: both;
    }

    a {
        color: inherit;
        transition: color .2s;
    }
    a:hover {
        @include th-var(color, --th-link-color, $link-color);
    }

    .quantity {
        display: block;
        font-weight: $font-weight-medium;
        margin-top: 4px;
        font-size: 13px;
    }
}
.woocommerce-mini-cart__total {
    font-size: 15px;
    margin-top: 16px;
    margin-bottom: 24px;
    display: flex;
    justify-content: space-between;

    strong {
        font-weight: $font-weight-medium;
    }
}
.woocommerce-mini-cart__buttons {
    display: flex;
    margin-bottom: 0;

    @include direction {
        #{$margin-inline-start}: -8px;
    }

    & > * {
        flex-grow: 1;

        @include direction {
            #{$margin-inline-start}: 8px;
        }
    }

    & > :not(.checkout) {
        @include th-button--style--secondary;
    }
}
.woocommerce-mini-cart__empty-message {
    margin: 0;
    text-align: center;
    font-size: 15px;
    padding: 12px 0;
}
