/*
// .th-search
*/

.th-search {}
.th-search__dropdown {
    $local-timing: cubic-bezier(0.645, 0.045, 0.355, 1);
    $local-duration: .25s;

    color: map_get($menu-scheme, opposite);
    background-color: map_get($menu-scheme, main);
    box-shadow: map_get($menu-scheme, shadow);
    position: absolute;
    top: 100%;
    border-radius: 1.5px;
    opacity: 0;
    transform: translateY(26px);
    visibility: hidden;
    pointer-events: none;
    transition:
        opacity $local-duration $local-timing,
        transform $local-duration $local-timing,
        visibility 0s $local-duration;

    @include direction {
        #{$inset-inline-start}: 0;
        #{$inset-inline-end}: 0;
    }
}
.th-search__dropdown--open {
    pointer-events: auto;
    opacity: 1;
    transform: translateY(0);
    visibility: visible;
    transition-delay: 0s;
}
.th-search__button-icon svg {
    display: block;
}
.th-search__dropdown--category-picker {
    max-width: 400px;
    min-width: 180px;
    width: fit-content;
    font-size: 15px;
    line-height: 28px;
    white-space: nowrap;
    max-height: calc(100vh - 240px);
    display: flex;

    .th-dropdown-list {
        display: block;
        padding: 12px 0;
        margin: 0;
        list-style: none;
        user-select: none;
        overflow-y: auto;
        max-height: 100%;
        flex-grow: 1;

        &:focus {
            outline: none;
        }
    }
    .th-dropdown-list__item {
        margin: 0;
        padding: 0 20px;
        cursor: default;
        display: flex;
        align-items: center;
    }
    .th-dropdown-list__item--current {
        background: #1e90ff;
        color: #fff;
    }
    .th-dropdown-list__item-padding {
        width: 16px;
        flex-shrink: 0;
    }
    .th-dropdown-list__item-title {
        overflow: hidden;
        text-overflow: ellipsis;
    }
}

@import 'th-search--header--spaceship';
@import 'th-search--header--classic';
