/*
// .th-indicator
*/

$local-open-transition-duration: .2s;

.th-indicator {
    position: relative;
}
.th-indicator__button {
    padding: 3px;
    display: flex;
    flex-direction: column;
    position: relative;
    height: 54px;
    min-width: 54px;
    color: inherit;
    border-radius: 2px;
    background: transparent;
    border: none;

    @include direction {
        #{$padding-inline-end}: 12px;
    }

    &:focus {
        outline: none;
    }
    &:hover {
        color: inherit;
        text-decoration: none;
    }
}
.th-indicator__icon {
    position: absolute;
    padding: 8px;
    display: block;
    width: 48px;
    height: 48px;

    svg {
        display: block;
        fill: currentColor;

        @include direction {
            transform: scaleX($transform-direction); // SKIP-RTL
        }
    }
}
.th-indicator__counter {
    position: absolute;
    top: 5px;

    @include direction {
        #{$inset-inline-end}: 4px;
    }

    @include th-indicator;
}
.th-indicator__title,
.th-indicator__value {
    display: block;

    @include direction {
        #{$margin-inline-start}: 50px;
    }
}
.th-indicator__title {
    padding-top: 3px;
    margin-bottom: -3px;
    font-size: 13px;
}
.th-indicator__value {
    font-size: 18px;
    font-weight: $font-weight-medium;
}
.th-indicator__content {
    top: 100%;
    position: absolute;
    visibility: hidden;
    transform-origin: top;
    transform: rotateX(60deg);
    opacity: 0;
    transition:
        transform $local-open-transition-duration,
        opacity $local-open-transition-duration,
        visibility 0s $local-open-transition-duration;

    @include direction {
        #{$inset-inline-end}: 0;
    }
}


/*
// .th-indicator--trigger--click.th-indicator--open,
// .th-indicator--trigger--hover:hover
*/


.th-indicator--trigger--click.th-indicator--open,
.th-indicator--trigger--hover:hover {
    .th-indicator__content {
        z-index: 1;
        transition-delay: 0s, 0s, 0s;
        opacity: 1;
        visibility: visible;
        transform: rotateX(0deg);
    }
}


.th-indicator--mobile {
    .th-indicator__button {
        padding: 0 0 2px;
        height: 50px;
        width: 44px;
        min-width: auto;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 0;
    }
    .th-indicator__icon {
        padding: 0;
        position: relative;
        height: auto;
        width: auto;
    }
    .th-indicator__counter {
        top: -7px;

        @include direction {
            #{$inset-inline-end}: -7px;
        }
    }
}


@mixin local-define-scheme($scheme) {
    .th-indicator__title {
        color: map_get($scheme, muted);
    }
    .th-indicator__value {
        @include scheme-font-weight($scheme, opposite, $font-weight-medium);
    }

    .th-indicator:hover,
    .th-indicator--open {
        .th-indicator__button {
            background: map_get($scheme, hover);
        }
    }
}


// define scheme
@include local-define-scheme($header-scheme);

.th-indicator__counter {
    color: map_get($indicator-counter-scheme, opposite);

    @include scheme-font-weight($indicator-counter-scheme, opposite, $font-weight-medium);

    &:before {
        background-color: map_get($indicator-counter-scheme, main);
    }
}
