/*
// .th-account-menu
*/

.th-account-menu {
    width: 280px;
    background-color: map_get($menu-scheme, main);
    box-shadow: map_get($menu-scheme, shadow);
    border-radius: 1.5px;
    color: map_get($menu-scheme, opposite);
}
.th-account-menu__divider {
    height: 1px;
    background: map_get($menu-scheme, divider);
}
.th-account-menu__user {
    display: flex;
    align-items: center;
    padding: 14px 20px;
    color: inherit;

    &:hover {
        color: inherit;
        background: map_get($menu-scheme, hover);
    }
}
.th-account-menu__user-avatar {
    width: 44px;
    flex-shrink: 0;

    @include direction {
        #{$margin-inline-end}: 14px;
    }

    img {
        border-radius: 100%;
        max-width: 100%;
    }
}
.th-account-menu__user-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
}
.th-account-menu__user-name {
    font-size: 15px;
    line-height: 20px;
}
.th-account-menu__user-email {
    font-size: 14px;
    line-height: 18px;
    color: map_get($menu-scheme, opposite-alt);
    margin-top: 1px;
}
.th-account-menu__links {
    list-style: none;
    padding: 12px 0;
    margin: 0;

    li {
        @include th-reset-list-item;
    }

    a {
        display: block;
        font-size: 15px;
        padding: 5px 20px;

        @include scheme-font-weight($menu-scheme, opposite, $font-weight-medium);
    }
    a:hover {
        background: map_get($menu-scheme, hover);
    }
    a,
    a:hover {
        color: inherit;
    }
}
