@mixin th-indicator {
    font-size: 10px;
    line-height: 1;
    padding: 2px 3px 1px;
    border-radius: (13px / 2);
    text-align: center;
    z-index: 0;

    &:before {
        display: block;
        position: absolute;
        content: '';
        left: 0; // SKIP-RTL
        top: 0;
        right: 0; // SKIP-RTL
        bottom: 0;
        z-index: -1;
        border-radius: 2.5px;

        @include direction {
            transform: skewX(#{-11deg * $transform-direction}); // SKIP-RTL
        }
    }
}
