/*
// .th-main-menu
*/

.th-main-menu {
    height: 100%;
}
.th-main-menu__list {
    position: relative;
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    flex-wrap: wrap;
    height: 100%;

    & > .menu-item {
        @include th-reset-list-item;

        position: relative;
    }
    & > .menu-item-megamenu {
        position: static;
    }
    & > .menu-item > a {
        display: block;
        padding: 5px 11px;
        border-radius: 2px;
        color: inherit;
        white-space: nowrap;
        font-size: 15px;

        &:hover {
            color: inherit;
        }
    }

    & > .menu-item > .sub-menu,
    & > .menu-item > .th-megamenu {
        color: map_get($menu-scheme, opposite);
    }
    & > .menu-item-has-children > a {
        $padding: 9px;
        $width: 5px;

        position: relative;

        @include direction {
            #{$padding-inline-end}: $padding * 2 + $width;
        }

        svg {
            top: 14px;
            position: absolute;
            fill: currentColor;

            @include direction {
                #{$inset-inline-end}: $padding + 1px;
            }
        }
    }

    & > .menu-item > .sub-menu {
        position: absolute;
        z-index: 1;
        top: 100%;
        opacity: 0;
        visibility: hidden;
        transform-origin: top;
        transform: rotateX(45deg);
        transition:
            transform .2s,
            opacity .2s;

        @include direction {
            #{$inset-inline-start}: 0;
        }
    }

    & > .menu-item:hover > .sub-menu {
        visibility: visible;
        opacity: 1;
        transform: rotateX(0);
    }

    & > .menu-item > .th-megamenu {
        position: absolute;
        z-index: 1;
        opacity: 0;
        visibility: hidden;
        transform-origin: top;
        transform: rotateX(45deg);
        transition:
            transform .2s,
            opacity .2s;
    }
    & > .menu-item:hover > .th-megamenu {
        visibility: visible;
        opacity: 1;
        transform: rotateX(0);
    }
}


$local-megamenu-sizes: (
    xxl: 6,
    xl:  5,
    lg:  4,
    md:  3,
    nl:  2,
    sm:  1,
);

.th-main-menu__list > .menu-item > .th-megamenu {
    padding: 26px 30px 30px;
    background-color: map_get($menu-scheme, main);
    box-shadow: map_get($menu-scheme, shadow);
}

@each $name, $size in $local-megamenu-sizes {
    .th-main-menu__list > .menu-item > .th-megamenu--size--#{$name} {
        @include media-breakpoint-up(xxl) {
            width: calc((#{map_get($container-max-widths, xxl) - $grid-gutter-width} - 30px) / 6 * #{$size} + 30px);
        }
        @include media-breakpoint-only(xl) {
            width: calc((#{map_get($container-max-widths, xl) - $grid-gutter-width} - 30px) / 6 * #{$size} + 30px);
        }
    }
}


@if($header-layout == spaceship) {
    @include media-breakpoint-only(xl) {
        .th-main-menu__list > .menu-item > a {
            $delta: -2px;

            @include direction {
                #{$padding-inline-start}: (11px + $delta);
                #{$padding-inline-end}: (23px + $delta);
            }

            svg {
                @include direction {
                    #{$inset-inline-end}: (10px + $delta / 2);
                }
            }
        }
    }
}


@if($header-layout == classic) {
    .th-main-menu__list > .menu-item > a {
        height: 100%;
        padding-top: 0;
        padding-bottom: 0;
        display: flex;
        align-items: center;
    }
    .th-main-menu__list > .menu-item-has-children > a svg {
        top: calc(50% - 2px);
    }
}

@mixin local-define-scheme($scheme) {
    .th-main-menu__list > .menu-item > a {
        @include scheme-font-weight($scheme, opposite, $font-weight-medium);
    }
    .th-main-menu__list > .menu-item-has-children > a svg {
        color: map_get($scheme, arrow-color);
    }
    .th-main-menu__list > .menu-item:hover > a {
        background-color: map_get($scheme, hover)
    }
}


// define scheme
@if($header-layout == spaceship) {
    @include local-define-scheme($navbar-scheme);
}
@if ($header-layout == classic) {
    @include local-define-scheme($navbar-scheme);

    @if ($navbar-stretch) {
        .th-main-menu__list > .menu-item > a {
            border-radius: 0;
        }
    }
}
