/*
// .th-vehicle-picker
*/

.th-vehicle-picker {}
.th-vehicle-picker__panel {
    display: none;
}
.th-vehicle-picker__panel--active {
    display: block;
}
.th-vehicle-picker__panel-body {
    $local-padding: 20px;

    padding: $local-padding $local-padding ($local-padding + 8px);
}
.th-vehicle-picker__text {
    font-size: 15px;
    color: map_get($menu-scheme, opposite-alt);
    margin-bottom: 8px;

    &:first-child {
        margin-top: -5px;
    }
}
.th-vehicle-picker__actions {
    display: flex;
    align-items: center;
    justify-content: flex-end;
    margin-top: 32px;

    & > * + * {
        @include direction {
            #{$margin-inline-start}: 20px;
        }
    }
}
.th-vehicle-picker__link {
    a {
        font-size: 14px;
        color: map_get($menu-scheme, muted);
    }
    a:hover {
        text-decoration: underline;
    }

    .th-garage-empty & {
        display: none;
    }
}
