@mixin th-loader-animation() {
    @keyframes th-loader-animation {
        from {
            transform: rotateZ(0deg);
        }
        to {
            transform: rotateZ(360deg);
        }
    }
}
@mixin th-loader-size($size) {
    left: calc(50% - #{$size / 2}); // SKIP-RTL
    top: calc(50% - #{$size / 2});
    width: $size;
    height: $size;
    border-radius: $size / 2;
}
@mixin th-loader-thickness($thickness) {
    border-width: $thickness;
}
@mixin th-loader-color($color) {
    border-color: rgba($color, .1);
    border-top-color: rgba($color, .5);
}
@mixin th-loader-base($size: null, $thickness: null, $color: null) {
    @if ($size != null) {
        @include th-loader-size($size);
    }
    @if ($thickness != null) {
        @include th-loader-thickness($thickness);
    }
    @if ($color != null) {
        @include th-loader-color($color);
    }

    border-style: solid;
    animation-name: th-loader-animation;
    animation-duration: .5s;
    animation-timing-function: linear;
    animation-iteration-count: infinite;
}
@mixin th-loader($size: null, $thickness: null, $color: null) {
    @include th-loader-base($size, $thickness, $color);

    position: absolute;
    display: block;
    content: '';
}
@mixin th-loader-overlay($bg-color, $expand-x, $expand-y, $size: null, $thickness: null, $color: null) {
    position: absolute;
    left: 0; // SKIP-RTL
    top: 0;
    width: 100%;
    height: 100%;

    &:before {
        position: absolute;
        display: block;
        content: '';
        left: -$expand-x; // SKIP-RTL
        top: -$expand-y;
        width: calc(100% + #{$expand-x * 2});
        height: calc(100% + #{$expand-y * 2});
        background: $bg-color;
        opacity: .9;
    }
    &:after {
        @include th-loader($size, $thickness, $color);
    }
}
