/*
// .th-megamenu
*/

.th-megamenu {
    position: relative;
}
.th-megamenu__row {
    padding: 0;
    margin-top: 0;
    margin-bottom: 0;
    list-style: none;
}
.th-megamenu__image {
    position: absolute;
    bottom: 0;

    @include direction {
        #{$inset-inline-end}: 0;
        transform-origin: bottom $inline-end;
    }

    img,
    picture {
        display: block;
    }

    @include media-breakpoint-only(xl) {
        transform: scale(.81);
    }
}
.th-megamenu__row  > .menu-item {
    margin: 0;
}
.th-megamenu__row  > .menu-item > .sub-menu {
    list-style: none;
    padding: 0;

    & > .menu-item {
        @include th-reset-list-item;

        line-height: 18px;
    }
    & > .menu-item + .menu-item {
        @include th-reset-list-item;

        margin-top: 10px;
    }
    & > .menu-item-has-children + .menu-item {
        margin-top: 24px;
    }
    & > .menu-item > a {
        font-size: 16px;
        color: inherit;
        transition: color .15s;

        @include scheme-font-weight($menu-scheme, opposite, $font-weight-medium);

        @include media-breakpoint-only(xl) {
            font-size: 15px;
        }

        &:hover {
            @include th-var(color, --th-link-color, $link-color);
        }
    }
    & > .menu-item > .sub-menu {
        border-top: 1px solid map_get($menu-scheme, divider);
        margin: 5px 0 0;
        padding: 12px 0 0;
        list-style: none;
    }
    & > .menu-item > .sub-menu > .menu-item {
        line-height: 16px;
    }
    & > .menu-item > .sub-menu > .menu-item + .menu-item {
        margin-top: 9px;
    }
    & > .menu-item > .sub-menu > .menu-item > a {
        font-weight: $font-weight-normal;
        font-size: 14px;
        color: map_get($menu-scheme, opposite-alt);

        &:hover {
            @include th-var(color, --th-link-color, $link-color);
        }
    }
}
