.wp-block-latest-comments {
  --th-avatar-size: 38px;
  --th-avatar-gutter: 14px;
  padding: 0;
}

.wp-block-latest-comments .avatar {
  width: var(--th-avatar-size);
  height: var(--th-avatar-size);
  border-radius: calc(var(--th-avatar-size) / 2);
  margin-left: 0;
  margin-right: var(--th-avatar-gutter);
  float: left;
}

.wp-block-latest-comments:not(.has-dates) {
  --th-avatar-size: 20px;
  --th-avatar-gutter: 10px;
}

.wp-block-latest-comments__comment {
  margin: 0;
}

.wp-block-latest-comments__comment + .wp-block-latest-comments__comment {
  margin-top: 8px;
}

.has-avatars .wp-block-latest-comments__comment + .wp-block-latest-comments__comment {
  margin-top: 12px;
}

.has-dates .wp-block-latest-comments__comment + .wp-block-latest-comments__comment,
.has-excerpts .wp-block-latest-comments__comment + .wp-block-latest-comments__comment {
  margin-top: 16px;
}

.wp-block-latest-comments__comment:after {
  display: block;
  content: '';
  clear: both;
}

.has-avatars .wp-block-latest-comments__comment {
  min-height: 0;
}

.has-avatars .wp-block-latest-comments__comment .wp-block-latest-comments__comment-meta {
  margin-left: calc(var(--th-avatar-size) + var(--th-avatar-gutter));
}

.has-avatars .wp-block-latest-comments__comment .wp-block-latest-comments__comment-meta:after {
  display: block;
  content: '';
  clear: both;
}

.wp-block-latest-comments__comment-excerpt,
.has-avatars .wp-block-latest-comments__comment .wp-block-latest-comments__comment-excerpt {
  margin: 12px 0 0;
  width: 100%;
  background: #f5f5f5;
  padding: 12px 17px;
  border-radius: 5px;
  font-size: 15px;
  position: relative;
}

.wp-block-latest-comments__comment-excerpt:before,
.has-avatars .wp-block-latest-comments__comment .wp-block-latest-comments__comment-excerpt:before {
  position: absolute;
  content: '';
  display: block;
  top: -10px;
  border: 5px solid transparent;
  border-bottom-color: #f5f5f5;
  pointer-events: none;
  left: calc(var(--th-avatar-size) / 2 - 5px);
}

.wp-block-latest-comments__comment-meta {
  font-size: 15px;
  line-height: 1.375;
  color: #262626;
}

.wp-block-latest-comments__comment-date {
  font-size: 13px;
  margin-top: 1px;
  color: #999;
}

.wp-block-latest-comments__comment-author {
  font-weight: 500;
  color: #262626;
  text-decoration: none;
}

.wp-block-latest-comments__comment-link {
  color: #262626;
  text-decoration: none;
}

.wp-block-latest-comments__comment-link:hover {
  text-decoration: underline;
}

.wp-block-latest-comments__comment-excerpt p {
  font-size: 0.9375em;
  line-height: inherit;
}

.wp-block-search .wp-block-search__label {
  font-size: 15px;
  font-weight: 400;
  margin-bottom: .25rem;
  display: block;
}

.wp-block-search .wp-block-search__input,
.wp-block-search.wp-block-search__button-inside .wp-block-search__inside-wrapper .wp-block-search__input {
  border-radius: 2px;
  background-clip: border-box;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out,background .15s ease-in-out;
  color: #262626;
  background-color: #ebebeb;
  border: 1px solid #ebebeb;
  padding: 7.5px 10px;
  font-size: 16px;
  line-height: 19px;
  min-height: 36px;
  vertical-align: middle;
  max-width: 100%;
  width: 100%;
  font-family: inherit;
  margin: 0;
}

.wp-block-search .wp-block-search__input:hover,
.wp-block-search.wp-block-search__button-inside .wp-block-search__inside-wrapper .wp-block-search__input:hover {
  border-color: #d9d9d9;
}

.wp-block-search .wp-block-search__input:focus,
.wp-block-search.wp-block-search__button-inside .wp-block-search__inside-wrapper .wp-block-search__input:focus {
  outline: none;
  border-color: #d9d9d9;
  background-color: #fff;
}

.wp-block-search .wp-block-search__button {
  position: relative;
  text-align: center;
  vertical-align: middle;
  display: inline-block;
  border: none;
  border-radius: 2px;
  fill: currentColor;
  font-weight: 500;
  cursor: pointer;
  transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out, box-shadow .15s ease-in-out;
  font-size: 15px;
  line-height: 19px;
  padding: 8.5px 16px;
  background: #e52727;
  background: var(--th-btn-primary-normal-bg-color);
  color: #fff;
  color: var(--th-btn-primary-normal-font-color);
  box-shadow: none;
  font-family: inherit;
  margin-left: 10px;
}

.wp-block-search .wp-block-search__button:focus {
  outline: none;
}

.wp-block-search .wp-block-search__button:disabled {
  cursor: default;
  opacity: .65;
}

.wp-block-search .wp-block-search__button:hover, .wp-block-search .wp-block-search__button:focus {
  background: #e52727;
  background: var(--th-btn-primary-hover-bg-color);
  color: #fff;
  color: var(--th-btn-primary-hover-font-color);
}

.wp-block-search .wp-block-search__button:active {
  background: #e52727;
  background: var(--th-btn-primary-active-bg-color);
  color: #fff;
  color: var(--th-btn-primary-active-font-color);
}

.wp-block-search .wp-block-search__button:disabled {
  background: #e52727;
  background: var(--th-btn-primary-normal-bg-color);
  color: #fff;
  color: var(--th-btn-primary-normal-font-color);
}

.wp-block-search .wp-block-search__button svg {
  margin: -4px;
  display: block;
}

.wp-block-search.wp-block-search__button-inside .wp-block-search__inside-wrapper {
  border: none;
  padding: 0;
}

.wp-block-search.wp-block-search__button-inside .wp-block-search__inside-wrapper .wp-block-search__button {
  padding: 8.5px 16px;
  margin-left: 0;
}

.wp-block-tag-cloud:not(.wp-block) {
  display: flex;
  flex-wrap: wrap;
  font-weight: 500;
  margin-top: -4px;
  margin-right: -4px;
}

.wp-block-tag-cloud:not(.wp-block) .tag-cloud-link {
  display: block;
  margin-top: 4px;
  background: #f2f2f2;
  padding: 2px 8px;
  color: inherit;
  border-radius: 1.5px;
  transition: background .12s;
  font-size: 13px !important;
  text-decoration: none;
  margin-right: 4px;
}

.wp-block-tag-cloud:not(.wp-block) .tag-cloud-link:hover {
  color: inherit;
  background: #e5e5e5;
}

.wp-block-tag-cloud:not(.wp-block) .tag-cloud-link:active {
  transition-duration: 0s;
  background: #d9d9d9;
}

ul.wp-block-archives-list,
.wp-block-categories ul {
  padding: 0;
}

.wp-block-archives-list,
.wp-block-archives-list ul,
.wp-block-categories-list,
.wp-block-categories-list ul,
.wp-block-categories > .wp-block-categories__list,
.wp-block-categories > .wp-block-categories__list ul {
  padding: 0;
  list-style: none;
  line-height: 20px;
  color: #999;
}

.wp-block-archives-list ul,
.wp-block-categories-list ul,
.wp-block-categories > .wp-block-categories__list ul {
  margin: 0;
}

.wp-block-archives-list li,
.wp-block-categories-list li,
.wp-block-categories > .wp-block-categories__list li {
  margin: 0;
  position: relative;
  font-size: 12px;
  line-height: 20px;
  padding-left: 14px;
}

.wp-block-archives-list > li:before,
.wp-block-categories-list > li:before,
.wp-block-categories > .wp-block-categories__list > li:before {
