/*
// .th-account-menu
*/
.th-account-menu {
  width: 280px;
  background-color: #fff;
  box-shadow: 0 2px 20px rgba(0, 0, 0, 0.2);
  border-radius: 1.5px;
  color: #262626;
}

.th-account-menu__divider {
  height: 1px;
  background: #ebebeb;
}

.th-account-menu__user {
  display: flex;
  align-items: center;
  padding: 14px 20px;
  color: inherit;
}

.th-account-menu__user:hover {
  color: inherit;
  background: #f2f2f2;
}

.th-account-menu__user-avatar {
  width: 44px;
  flex-shrink: 0;
  margin-right: 14px;
}

.th-account-menu__user-avatar img {
  border-radius: 100%;
  max-width: 100%;
}

.th-account-menu__user-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.th-account-menu__user-name {
  font-size: 15px;
  line-height: 20px;
}

.th-account-menu__user-email {
  font-size: 14px;
  line-height: 18px;
  color: #6c757d;
  margin-top: 1px;
}

.th-account-menu__links {
  list-style: none;
  padding: 12px 0;
  margin: 0;
}

.th-account-menu__links li {
  margin: 0;
}

.th-account-menu__links a {
  display: block;
  font-size: 15px;
  padding: 5px 20px;
  font-weight: 500;
}

.th-account-menu__links a:hover {
  background: #f2f2f2;
}

.th-account-menu__links a,
.th-account-menu__links a:hover {
  color: inherit;
}

/*
// .th-departments
*/
.th-departments {
  position: relative;
}

.th-departments__button {
  position: relative;
  fill: currentColor;
  border: none;
  border-radius: 2px;
  font-family: inherit;
  font-size: 15px;
  padding-top: 5px;
  padding-bottom: 5px;
  transition: background-color .2s, color .2s;
  padding-left: 35px;
  padding-right: 11px;
  text-align: left;
}

.th-departments__button:focus {
  outline: none;
}

.th-departments__button-icon {
  top: 10px;
  position: absolute;
  fill: currentColor;
  display: inline-block;
  transition: color .2s;
  left: 10px;
  transform: scaleX(1);
}

.th-departments__button-icon svg {
  display: block;
}

.th-departments__button-arrow {
  display: none;
  fill: currentColor;
  position: absolute;
  top: calc(50% - 4px);
  transition: transform .2s, color .2s;
  right: 14px;
}

.th-departments__button-arrow svg {
  display: block;
}

.th-departments__menu {
  position: absolute;
  z-index: 1;
  visibility: hidden;
  opacity: 0;
  top: 100%;
  transform: translateY(-10px);
  transition: opacity .2s, transform .2s, visibility 0s .2s;
}

.th-departments__body {
  border-radius: 1.5px;
  pointer-events: auto;
  display: flex;
  background-color: #fff;
  box-shadow: 0 2px 20px rgba(0, 0, 0, 0.2);
  color: #262626;
  float: left;
}

.th-departments__list {
  flex-shrink: 0;
  list-style: none;
  margin: 0;
  padding: 0;
  font-size: 15px;
  line-height: 20px;
  font-weight: 500;
}

.th-departments__list > .menu-item {
  margin: 0;
}

.th-departments__list > .menu-item > a {
  color: inherit;
  display: block;
  position: relative;
}

.th-departments__list > .menu-item-hover > a {
  background: #f2f2f2;
  color: inherit;
}

.th-departments__list > .menu-item > a > .th-menu-item-arrow {
  fill: #bfbfbf;
  position: absolute;
  top: calc(50% - 6px);
}

.th-departments__list > .menu-item > a > .th-menu-item-arrow svg {
  display: block;
}

.th-departments__list > .menu-item > .th-megamenu {
  display: none;
}

.th-departments__menu-container > .th-megamenu {
  min-height: 100%;
  display: none;
  box-shadow: 1px 0 #ebebeb inset;
}

.th-departments__menu-container > .th-megamenu--open {
  display: block;
}

.th-departments__arrow {
  position: absolute;
  width: 31px;
  height: 21px;
  pointer-events: none;
  bottom: 100%;
  overflow: hidden;
}

.th-departments__arrow:before {
  position: absolute;
  display: block;
  content: '';
  width: 7px;
  height: 7px;
  top: 100%;
  margin-top: -3.5px;
  background-color: #fff;
  box-shadow: 0 2px 20px rgba(0, 0, 0, 0.2);
  border-radius: 1px;
  left: 50%;
  margin-left: -3.5px;
  transform: rotate(45deg) translateX(-0.5px) translateY(-0.5px);
}

.th-departments--open .th-departments__menu {
  transition-delay: 0s;
  visibility: visible;
  opacity: 1;
  transform: translateY(0);
}

.th-departments--open .th-departments__button-arrow {
  transform: rotate(180deg);
}

.th-departments__button {
  height: 100%;
  width: 230px;
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 43px;
  padding-right: 37px;
}

@media (min-width: 1200px) and (max-width: 1399.98px) {
  .th-departments__button {
    width: 210px;
  }
