.th-indicator__counter {
  position: absolute;
  top: 5px;
  right: 4px;
  font-size: 10px;
  line-height: 1;
  padding: 2px 3px 1px;
  border-radius: 6.5px;
  text-align: center;
  z-index: 0;
}

.th-indicator__counter:before {
  display: block;
  position: absolute;
  content: '';
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  border-radius: 2.5px;
  transform: skewX(-11deg);
}

